.contacts .title {
    font-size: 32px;
    line-height: normal;
}

.contacts {
    margin-top: 60px;
    padding: 0 16px;
}

.contact-wrap {
    display: flex;
}

.contact-field {
    border-bottom: 1px solid #D5DAE0;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    flex: 1;
}

.contact-icon {
    padding-left: 4px;
    padding-right: 20px;
    padding-bottom: 20px;
    display: flex;
    align-items: center;
}

.contact-icon-empty {
    width: 24px;
    height: 24px;
}

.contact-title {
    font-family: Roboto Slab,sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 32px;
}

.contact-text {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    color: #6A6A6A;
}

.underline {
    text-decoration: underline;
}